let users = createSlice({
    name: 'users',
    initialState:[
        {
            email: 'fallingstar@gmail.com',
            name: '',
            password: 'dudnjs725@',
            phone:'01052394217',
            birth: '010725'
        },
    ],
    reducers:{
        addUser(state, action){
            state.push(action.payload)
        }
    }
})
export let {addUser} = users.actions


let will_list = createSlice({
    name: 'will_list',
    initialState:[
        {
            name: '',
            birth: '010725-1234567',
            question1: ' 3 ',
            question2: [
                {
                    name: ' ',
                    relationship: ' ',
                    phone: ' '
                },
            ],
            question3: '',
            question4: false,
            question5: true,
            family: {
                child:', ',
                parents:', ',
                sibile:', ',
                uncle:', ',
                spouse:', '
            },
            property:{
                house: 1,
                bond:2,
                patent: 0,
                car: 0
            },
            text: '',
            open: {
                opener:'',
                contact: '010-1234-1234',
                relation: ''
            }
        },

    ],
    reducers:{
        addWill(state, action){
            state.push(action.payload);
        }
    }
})
export let {addWill} = will_list.actions


// 
const reducers = combineReducers({
    will : will.reducer,
    users: users.reducer,
    will_list: will_list.reducer,
    login_user: login_user.reducer,
});

//redux-persist
const persistConfig = {
    key: 'root',
    storage
};

const persistedReducer = persistReducer(persistConfig, reducers);

const store = configureStore({
    reducer: persistedReducer, 
    devTools: process.env.NODE_ENV !== 'production',
    middleware: [thunk],
});
export default store;